import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/swiper.scss";

import "./assets/css/app.css";

export default function App() {
  const [currentPage, setCurrentPage] = React.useState<number>(0);

  const slides = Array.from({ length: 611 }).map((el, index) => {
    return (
      <SwiperSlide key={`el_${index}`} virtualIndex={index}>
        <img
          src={`${process.env.PUBLIC_URL}/assets/images/quran/${index}.png`}
          alt={`page ${index}`}
          style={{ width: "100%" }}
        />
      </SwiperSlide>
    );
  });

  return (
    <div className="App">
      <section>
        <Swiper
          spaceBetween={0}
          slidesPerView={1}
          onSlideChange={() => setCurrentPage(currentPage + 1)}
          onSwiper={(swiper: any) => {
            setCurrentPage(currentPage + 1);
          }}
        >
          {slides}
        </Swiper>
      </section>
    </div>
  );
}
